@import "../../scss/variables";

.container {
  min-height: 20vh;
  box-shadow: 5px 2px 30px -10px rgb(177, 174, 174);
  padding: 1rem 2rem;

  &.pink-card {
    background-image: linear-gradient(
      to bottom left,
      rgb(240, 213, 255),
      white
    );
    padding: 4rem 1rem 3rem 1rem;
    h1 {
      text-align: center;
      font-size: 3rem;
      padding-bottom: 4rem;
    }
  }
  &.white {
    background-color: rgb(255, 255, 255);
    color: rgb(69, 69, 69);
    box-shadow: none;
  }
  &.dark-blue {
    background-color: $blue-dark2;
    background-image: linear-gradient(to bottom right, $blue-dark2, $blue-dark);

    color: rgb(255, 255, 255);
    padding: 4rem 1rem 6rem 1rem;
    h1 {
      text-align: center;
      margin-bottom: 5rem;
      font-size: 3rem;
    }
    h2 {
      color: rgb(255, 255, 255);
      font-size: 2rem;
      font-weight: 300;
      color: $pink-dark2;
    }
    h4 {
      font-size: 1.2rem;
      margin: 0 4rem;
      align-self: center;

      @include respond(med) {
        font-size: 1.6rem;
      }
    }
  }
}
